import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';

import tranEn from './Files/en.json';
import tranKo from './Files/ko.json';
import tranJp from './Files/jp.json';
import tranId from './Files/id.json';

const resources = {
  en: { translation: tranEn },
  ko: { translation: tranKo },
  jp: { translation: tranJp },
  id: { translation: tranId },
}

const userLanguage = window.navigator.language || window.navigator.userLanguage;

i18n.use(initReactI18next).init({
  resources,
  lng: userLanguage || 'en',
  fallbackLng: 'en',
  keySeparator: false,
  interpolation: {
    escapeValue: false
  }
})

export default i18n;