const makeResult = (answer) => {
    const result = JSON.parse(answer
        .replaceAll('A', '"')
        .replaceAll('B', ',')
        .replaceAll('C', ':')
        .replaceAll('D', '{')
        .replaceAll('E', '}')
        .replaceAll('F', '[')
        .replaceAll('G', ']'));
    const nodes = JSON.parse(sessionStorage.getItem('984645163'));

    for (let i=0; i<names.length; i++) {
        
        let scoreSum = 0;

        nodes[i].forEach((v, index) => {
            if (index != 13) {
                const userNode = parseInt(result[index]);
                let tempScore = parseInt(v[userNode]);

                switch (index) {
                case 4:
                    tempScore *= 4;
                    break;
                case 7:
                case 3:
                    tempScore *= 2;
                    break;
                case 2:
                    tempScore *= 3/2;
                    break;
                case 5:
                case 6:
                case 8:
                    tempScore *= (2.5)/2;
                    break;
                }
                scoreSum += tempScore;
            }
        });
        nodes[i] = {
            H: i,
            I: parseInt(scoreSum / 38.5 * 10000),
            J: nodes[i]
        };
    }

    let resultNodes = nodes.sort((a, b) => b.I - a.I);
    resultNodes.forEach((v, index) => {
        if (index != 0) {
            delete v.J;
        }
    })

    resultNodes = resultNodes.slice(0,20);

    return resultNodes;
}