import '../styles/Feedback.css';
import { useSearchParams, useNavigate } from 'react-router-dom';
import $ from 'jquery';
window.$ = $;

export default function Feedback() {

    const [searchParams, setSearchParams] = useSearchParams();
    const navigate = useNavigate();

    const names = [" ", "", "", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", "", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", "", " ", " ", " ", " ", " ", " ", " ", " ", " ", " ", "  ", " ", " ", " ", " ", " ", " ", " ", " "];

    const sendFeedback = (id) => {
        if (localStorage.getItem('861351681') == null) {
            const ans = JSON.parse(searchParams.get('hash')
                .replaceAll('A', '"')
                .replaceAll('B', ',')
                .replaceAll('C', ':')
                .replaceAll('D', '{')
                .replaceAll('E', '}')
                .replaceAll('F', '[')
                .replaceAll('G', ']'));
        
            $.ajax({
            url: "https://script.google.com/macros/s/AKfycbxNyXeUxXW3EXkUGfYtzHL44IYFESgBA38hNmXNpULEFHA8O5tNXGl46NmL0yhkZstafA/exec",
            data: {
            num1 : ans[0], num2 : ans[1], num3 : ans[2], num4 : ans[3], num5 : ans[4], num6 : ans[5], num7 : ans[6], num8 : ans[7], num9 : ans[8], num10 : ans[9], num11 : ans[10], num12 : ans[11], num14 : ans[12],
            num13 : names[id]
            },
            type: "POST"
            });
            localStorage.setItem('861351681', 'google');
        }
        alert(" !");
        navigate(-1);
    }

    const memList = names.map((v, i) => (
        <img
            src={ require('../assets/images/members/'+v+'.png') }
            id={i}
            className='memImage'
            onClick={() => sendFeedback(i)}
        />
    ));

    return (
        <div className="Feedback">
            <h1 className='text'>  !</h1>
            {memList}
        </div>
    );
}